
*, *::after, *::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: #fff;
}

body {
  font-family: 'Poppins', 'Montserrat', sans-serif;
  font-size: 16px;
  box-sizing: border-box;
  background-color: #111;
  color: #fff;
}

.px3 {
  padding-left: 3rem;
  padding-right: 3rem;
}

.input-field {
  width: 80%;
}

.todo-list {
  width: 80%;
  margin-top: 30px;
}

.date {
  font-size: 34px;
  font-weight: 600;
  margin: 50px 0 20px;
}

.total-tasks {
  color: #6e6e6e;
  font-weight: 500;
}

.todo {
  margin-bottom: 1rem;
}

.todo label {
  height: max-content;
}

[type="checkbox"] + span:not(.lever) {
  height: max-content;
}

.todo span {
  width: 80%;
  word-break: break-word;
}

.todo.completed span {
  text-decoration: line-through;

}

.todo label {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  padding: 1rem;
  background-color: #393939;
  border-radius: 5px;
}

.todo i {
  z-index: 100;
}


.container {
  width: 75%;
}

.todo-container {
  width: 90%;
  margin: 25px auto 0;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.my10 {
  margin: 10px 0;
}

.about-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 100px;

  .about {
    width: 50%;
    height: max-content;
    padding: 20px;
    background: #393939;

    .about-title {
      background: #6e6e6e;
      padding: 10px;
      font-size: 34px;
    }

    .about-text {
      font-weight: 500;
      color: white;
      text-align: center;
    }
  }
}

@media only screen and (max-width: 992px) {
  .hide-on-med-and-down {
    display: flex !important;
  }
  nav .brand-logo {
    left: 0;
    transform: translateX(0);
    padding: 0 0 0 30px;
  }
  .about-container {
    .about {
      width: 90%;

    }
  }
}

